<template><section><h1>CustomDataSource</h1>
<p>The <code v-pre>vc-datasource-custom</code> component is used to load a dataSource which can be used to manually manage a group of entities.</p>
<h2>Example</h2>
<h3>Load a CustomDataSource with <code v-pre>vc-datasource-custom</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-datasource-custom ref="datasource" name="custom">
          <vc-entity ref="entity1" :position="position" :billboard="billboard" :description="description" :id="id"> </vc-entity>
          <vc-entity ref="enttiy2" :position="position1" :description="description" :cylinder.sync="cylinder1">
            <vc-graphics-cylinder ref="cylinder1" :length="400000.0" :topRadius="200000.0" :bottomRadius="200000.0" :material="material1" :outline="true" :outlineColor="outlineColor1"></vc-graphics-cylinder>
          </vc-entity>
          <vc-entity :position="position2" :description="description" :cylinder.sync="cylinder2">
            <vc-graphics-cylinder ref="cylinder2" :length="400000.0" :topRadius="0.0" :bottomRadius="200000.0" :material="material2"></vc-graphics-cylinder>
          </vc-entity>
        </vc-datasource-custom>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
