      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([
        <span class="hljs-keyword">this</span>.$refs.entity1.createPromise,
        <span class="hljs-keyword">this</span>.$refs.cylinder1.createPromise,
        <span class="hljs-keyword">this</span>.$refs.cylinder2.createPromise
      ]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
